@font-face {
  font-family: "OpenSans-Regular";
  font-weight: normal;
  src: local("OpenSans-Regular"), url("../../fonts/OpenSans-Regular.woff2") format("woff2"), url("../../fonts/OpenSans-Regular.woff") format("woff");
}
@font-face {
  font-family: "OpenSans-SemiBold";
  font-weight: normal;
  src: local("OpenSans-SemiBold"), url("../../fonts/OpenSans-SemiBold.woff2") format("woff2"), url("../../fonts/OpenSans-SemiBold.woff") format("woff");
}
h1,
h2,
h3 {
  font-weight: 700;
  line-height: 1.1;
  margin-top: 0;
}

h1 {
  font-size: clamp(1.75rem, 1rem + 2vw, 2.5rem);
}

h2 {
  font-size: clamp(1.375rem, 0.875rem + 1.5vw, 2.25rem);
}

p {
  margin-top: 0;
  font-size: 1rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.intro > p {
  font-family: "OpenSans-SemiBold";
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  background-color: var(--defBackground);
  color: var(--defTextColor);
}

nav {
  background-color: var(--defNavBackground);
}

:root {
  --defBackground: lightgray;
  --defNavBackground: Darkgray;
  --navBgColorLight: darkgreen;
  --navBgColorDark: lightgreen;
  --defTextColor: white;
}

.grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  gap: 2.5rem;
  width: min(100% - 2.5rem, 62.5rem);
  margin-inline: auto;
}
@media (min-width: 56.25em) {
  .grid {
    grid-template-columns: 2fr 1fr;
    grid-template-rows: auto;
  }
}
.grid__main, .grid__aside {
  padding: 2.5rem;
}
.grid__main {
  background-color: hsl(240, 99%, 34%);
}
@media (min-width: 43.75em) {
  .grid__main {
    background-color: hsl(118, 72%, 21%);
  }
}
@media (min-width: 56.25em) {
  .grid__main {
    background-color: hsl(32, 83%, 40%);
  }
}
.grid__aside {
  background-color: hsl(300, 100%, 30%);
}
@media (max-width: 56.24875em) {
  .grid__aside {
    text-align: center;
  }
}

.topnav {
  background-color: var(--navBgColorLight, red);
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.topnav p {
  padding: 0 10px;
  margin: 0;
  display: flex;
}

.topnav ul {
  display: flex;
  list-style: none;
  margin-right: 10px;
  padding: 0;
  flex-wrap: wrap;
}

.topnav ul li {
  margin-left: 20px;
}

.topnav ul li a {
  color: white;
  text-decoration: none;
  display: inline-block;
}

