.tools {
  margin: 2rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
}

.tools p {
  background-color: lightgreen;
  padding: 10px;
  border-radius: 8px;
}

.tools p a {
  text-decoration: none;
  color: black;
}

.pictures {
  margin: 2rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
}

.pictures img {
  border-radius: 8px;
  width: 100%;
}

